.App {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 2rem;
  max-width: 800px;
  margin: auto;
  background-color: #fdfdfd;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

h1 {
  text-align: center;
  color: #333;
  font-size: 2rem;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

label {
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #444;
}

input,
select {
  padding: 0.6rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

input:focus,
select:focus {
  border-color: #007bff;
  outline: none;
}

.submit-btn {
  grid-column: 1 / -1;
  padding: 0.75rem 1rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 1rem;
  transition: background-color 0.3s ease;
}

.submit-btn:hover {
  background-color: #0056b3;
}

.result {
  margin-top: 2rem;
  padding: 1rem;
  background-color: #e6f7ff;
  border-left: 6px solid #1890ff;
  font-size: 1.2rem;
  border-radius: 6px;
  color: #004085;
}

@media (max-width: 600px) {
  .submit-btn {
    font-size: 0.9rem;
  }

  .result {
    font-size: 1rem;
  }
}
